@import "../../../assets/styles/vars";
@import "../../../assets/styles/mixins";

.wrap {
  width: 413px;
  margin: 0 auto;
  top: 60px;
  position: relative;
  background-color: #f9f9fe;
  border-radius: 8px;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 413px;
  width: 100%;
}
.login {
  height: 100vh;
  background: linear-gradient(180deg, #e6d4de 0%, #9890c7 100%);
}
.title,
.subtitle {
  font-family: Poppins;
  font-weight: 600;
}
.title {
  @include text(26px, 600, 36px, #2d2e46);
  margin: 25px 0 32px;
}
.subtitle {
  @include text(22px, 600, 33px, #2d2e46);
  margin-bottom: 38px;
}
.email {
  margin-bottom: 24px;
}

.textTop,
.textBottom {
  position: relative;
  font-family: SF UI Display;
  @include text(13px, 400, 20px, #24254a);
  opacity: 0.5;
}
.textTop {
  right: 156px;
}
.textBottom {
  right: 146px;
}
.linkNewPass,
.checkboxText {
  font-family: SF UI Display;
  @include text(14px, 400, 37px, #2d2e46);
}
.linkNewPass {
  margin-top: 24px;
  position: relative;
  left: 116px;
}

.btn {
  width: 266px;
  height: 36px;
  background: #21268f;
  box-shadow: 0px 4px 18px rgba(33, 38, 143, 0.35),
    inset 0px 1px 0px rgba(255, 255, 255, 0.3);
  border-radius: 30px;
  color: #ececf9;
  margin-top: 92px;
  &:hover {
    background: #43447b;
  }
}
.wrapLink {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 38px;
}
.linkNewAcc {
  font-family: SF UI Display;
  @include text(14px, 600, 24px, #2d2e46);
  opacity: 0.5;
}
.linkReg {
  font-family: SF UI Display;
  @include text(16px, 600, 24px, $accentColor);
  margin: 10px 0 30px;
}

.checkboxAndFogot {
  display: flex;
  align-items: center;
  position: relative;
}

.checkbox {
  display: flex;
  position: absolute;
  top: 22px;
  right: 80px;
  width: 150px;
}
.checkboxText {
  position: absolute;
  top: 2px;
  left: 36px;
}
